.inputContainer {
  position: relative;
  margin-bottom: 24px;
}

.input {
  display: block;
  width: 100%;
  height: 64px;
  background: #2F2F37!important;
  border: 1px solid #2F2F37;
  border-radius: 40px;
  padding: 5px 72px 5px 24px;
  color: #fff;
  font-family: 'Jet Brains Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: 600;
  letter-spacing: 1px;
  outline: none;
  -webkit-appearance: none;
  transition: border-color .3s ease;
  box-shadow: none!important;
  -webkit-text-fill-color: #fff;
}

.input:-webkit-autofill, 
.input:-webkit-autofill:hover, 
.input:-webkit-autofill:focus, 
.input:-webkit-autofill:active {
  -webkit-box-shadow: inset 0 0 0 35px #2F2F37!important;
}

@-webkit-keyframes autofill {
  to {
    color: inherit;
    background: transparent;
  }
}

.input:-webkit-autofill {
  -webkit-animation-name: autofill;
  -webkit-animation-fill-mode: both;  
}

.input::placeholder {
  color: #8585AD;
}

.input:focus {
  border-color: #fff;
}

@media (max-width: 767px){
  .inputContainer {
    margin-bottom: 20px;
  }
  .input {
    font-size: 16px;
    height: 48px;
    padding: 5px 56px 5px 20px;
  }
}